import React, { useState } from 'react'
import './Counter.css';

export default function Counter() {
    const [value, setValue] = useState("")
    const [faktor1, setFaktor1] = useState(Math.floor(Math.random() * 11))
    const [faktor2, setFaktor2] = useState(Math.floor(Math.random() * 11))
    const [rightCounter, setRightCounter] = useState(0)
    const [wrongCounter, setWrongCounter] = useState(0)
    const [rightCounterDiv, setRightCounterDiv] = useState(0)
    const [wrongCounterDiv, setWrongCounterDiv] = useState(0)
    const [checkboxValue, setCheckboxValue] = useState(false)
    const [divi1, setDivi1] = useState(faktor1*faktor2)
    const [divi2, setDivi2] = useState(faktor2)
    const [operand, setOperand] = useState("x")

    let sum = faktor1 * faktor2

    const handleCheckbox = e => {
        if (checkboxValue == false) {
            setCheckboxValue(!checkboxValue)
            setOperand(":")
            // setFaktor1(divi1);
            // setFaktor2(divi2);
            setDivi1(Math.floor(Math.random() * 10)+1);
            setDivi2(Math.floor(Math.random() * 10)+1);
            setFaktor1(divi1*divi2)
            setFaktor2(divi2)
        } else {
            setCheckboxValue(!checkboxValue)
            setOperand("x")
            setFaktor1(Math.floor(Math.random() * 11));
            setFaktor2(Math.floor(Math.random() * 11));
        }
    };

    const handleChange = e => {
        setValue(e.target.value);
    };
    const handleSubmit = e => {
        if (checkboxValue == false) {
            if (faktor1 * faktor2 == value) {
                setFaktor1(Math.floor(Math.random() * 11));
                setFaktor2(Math.floor(Math.random() * 11));
                setRightCounter(rightCounter+1);            
            } else {
                setWrongCounter(wrongCounter+1);
            };       
        } else {
            if (faktor1*faktor2/faktor2/faktor2 == value) {
                setDivi1(Math.floor(Math.random() * 10)+1);
                setDivi2(Math.floor(Math.random() * 10)+1);
                setFaktor1(divi1*divi2)
                setFaktor2(divi2)                
                setRightCounterDiv(rightCounterDiv+1);            
            } else {
                setWrongCounterDiv(wrongCounterDiv+1);
            };               
        }
            setValue("");
    };

    const handleKeypress = e => {
        if (e.key === 'Enter') {
            handleSubmit();
        }
    };

    return (
        <div>
            <h1>{ faktor1 } {operand} { faktor2 } =</h1>
            <input value={value} type="number" onKeyPress={handleKeypress} onChange={handleChange}/>
            <div>
            <button onClick={handleSubmit} type="submit">klick</button>
            </div>
            <div className="counter1">
                <div className="counter1">
                    Mal &#9745; 	: { rightCounter }                    
                </div>
                <div>
                    Mal &#9746; 	: { wrongCounter }
                </div>
            </div>
            <div className="counter">
                <div className="counter">
                    Geteilt &#9745; 	: { rightCounterDiv }                    
                </div>
                <div>
                    Geteilt &#9746; 	: { wrongCounterDiv }
                </div>
            </div>

            <form className="checkbox">
                <label>Dividieren:</label>
                <input 
                    type="checkbox" 
                    className="checkbox"
                    defaultChecked={checkboxValue}
                    onChange={handleCheckbox}
                />
            </form>
        </div>
    )
}